import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/exam-web/',
		name: 'Home',
		component: Home,
	},
	{
		path: '/exam-web/animals',
		name: 'Animals',
		component: () => import('../views/Animals.vue'),
	},
	{
		path: '/exam-web/about',
		name: 'About',
		component: () => import('../views/About.vue'),
	},
]

const router = new VueRouter({
	mode: 'history',
	routes,
})

export default router
